function ExpandToPanelCard({ className, citation, onSelectCitation, relatedSearchResult, }) {
    const serviceManager = useServiceManager();
    const { title } = getSearchResultMetaData(citation);
    // If there's a searchResult that isn't empty than the card is expandable. Otherwise The CitationCardContent component
    // will measure itself with the citation text as its content and will let this component know if it can fit the
    // contents in the tile. If it can't fit the contents then it needs to be a clickable tile that can expand into a
    // panel.
    const [isExpandable, setIsExpandable] = useState(Boolean(relatedSearchResult?.body));
    function onViewSourcePanelButtonClick() {
        // If a search result is provided we want to show that in the panel with the citation text highlighted, otherwise
        // just show the citation.
        serviceManager.store.dispatch(actions.setViewSourcePanelIsOpen(true, citation, relatedSearchResult));
    }
    function renderTile(className) {
        return (React.createElement(Tile, { className: className },
            React.createElement(CitationCardContent, { citation: citation, type: CitationType.EXPAND_IF_NEEDED, setIsExpandable: setIsExpandable, isExpandable: isExpandable })));
    }
    if (isExpandable) {
        return (React.createElement(CitationClickableCard, { className: className, title: title, onClick: onViewSourcePanelButtonClick, onSelectCitation: onSelectCitation }, renderTile()));
    }
    return renderTile(className);
}

export { ExpandToPanelCard };
